.header-content {
  background-image: url(../../img/header-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  position: relative;

  &::before {
    content: url(../../img/shoe-bg.png);
    z-index: 20;
    position: absolute;
    right: 48px;
    top: 100px;

    @include mobile{
      // right: -242px;
      display: none;
    }
  }
  @include mobile{
    background-position: top center;
    width: 100%;
  }
}

.header-content__inner {
  max-width: 575px;
  padding-top: 220px;
  position: relative;
  z-index: 10000;

  @include tablet {

    margin: 0 auto;
    text-align: center;
    right: 0;
    left: 0;
  }
  @include mobile{

    padding-top: 90px;
  }
}

.header__subtitle {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  color: #00A79D;
  // position: relative;
  margin: 0;
  width: 113px;
  border-bottom: 1px solid #00A79D;

  @include tablet {

    margin: 0 auto;
  }
}

.header__title {


  font-style: normal;
  font-weight: 400;
  font-size: 126px;
  line-height: 125px;
  text-transform: uppercase;
  color: #1F170A;
  margin: 20px 0 0 0;

@include mobile {

  font-size: 100px;
}

}

.header__price {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 35px;
  line-height: 41px;
  text-transform: uppercase;
  color: #FF9A00;
  margin: 50px 0 0 0;

  @include mobile{

    margin: 10px 0 0 0;
  }
}


.header__btn {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 26px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 20px 37px;
  background: linear-gradient(107.81deg, #0047FF -0.31%, #EC00E2 100%);
  box-shadow: 0px 40px 60px rgba(95, 42, 243, 0.2);
  margin-top: 40px;

  &::after {
    content: url(../../img/svg/arrow-btn.svg);
    position: relative;
    left: 15px;
  }

  @include mobile{

    margin-top: 20px;
  }
}

.scroll__icon {
  position: absolute;
  right: 0;
  bottom: 160px;
  z-index: 10001;

  &::before {
    content: '';
    width: 36px;
    top: 72px;
    left: 16px;
    border-bottom: 1px solid #ffffff6b;
    transform: rotate(90deg);
    position: absolute;
  }
}

.mouse__scroll {
  position: relative;
  top: 80px;
  left: 29px;

}

.scroll__down {
  transform: rotate(90deg);
  position: relative;
  margin: 0;
}

.social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  right: 0;
  bottom: 0;
  background: linear-gradient(273.64deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0.12) 93.23%, rgba(255, 255, 255, 0.24) 100%);
  opacity: 0.6;
  backdrop-filter: blur(370px);
  padding: 10px;
  margin: 0;
}

.social__link:nth-child(1n+1) {
  margin-left: 30px;
}
