
.header{
  height: 70px;
}

.header__content {
  display: flex;
  height: 67px;
  align-items: center;
}

.header__logo{
  margin-left: 10px;
}

.form__label {

  @include mobile {

    position: relative;
    left: 110px;
  }
  &::after {
    content: url(../../img/svg/search.svg);
    position: relative;
    top: 3px;
    right: 30px;
    cursor: pointer;
  }
}


.form__input {
  width: 70px;
  margin-right: 30px;
  position: relative;

  @include mobile {

    width: 60px;
    margin-right: 5px;
  }
}

.header__pagination {
  display: flex;
  align-items: center;
  right: 222px;
  position: absolute;
}

.header__heart-img {
  position: relative;
  left: 50px;
  cursor: pointer;

  @include mobile {

    left: 91px;
  }
}

.header__basket-img {
  position: relative;
  left: 100px;
  cursor: pointer;
}




