.features {
  background-image: url(../../img/sb-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
}


.features__content {
  max-width: 515px;
  position: absolute;
  right: 208px;

  @include small-tablet{

    max-width: 100%;
    // right: 208px;
    text-align: center;
    margin: 0 auto;
    // display: block;
    left: 0;
    right: 0;
  }
}

.features__content-subtitle {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 20px;
  margin: 150px 0 0 0;
  line-height: 23px;
  text-transform: uppercase;
  background: linear-gradient(45deg, #0B2349 33%, #0D61BC 66%, #8AA9D6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #0B2349;

  @include mobile{

    margin: 200px 0 0 0;
  }
}

.features__content-title {
  font-style: normal;
  font-weight: 400;
  font-size: 90px;
  line-height: 95px;
  text-transform: uppercase;
  color: #1F170A;
  margin: 20px 0 0 0;
}

.features__content-text {
  font-family: "Gilroy", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  text-transform: capitalize;
  margin: 30px 0 0 0;
}

.features__content-colurs {
  display: flex;
  align-items: center;
  margin-top: 50px;

  @include tablet{

    margin-top: 10px;
    justify-content: center;
  }
}

.text__colurs {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  text-transform: capitalize;
  color: #1F170A;
  margin: 0;
}


.img__colurs {
  margin-left: 15px;
  cursor: pointer;
}

.features__content-size {
  display: flex;
  align-items: center;
  margin-top: 50px;

  @include tablet{

    margin-top: 10px;
    justify-content: center;
  }
}

.text__size {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  text-transform: capitalize;
  color: #1F170A;
}

.form__input-size {
  width: 60px;
  margin-left: 15px;
}

.features__content-btn--one {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  margin-top: 50px;
  text-transform: uppercase;
  color: #FFFFFF;
  width: 176px;
  margin-right: 30px;
  padding: 21px 28px;
  transition: all .3s ease;
  background: linear-gradient(108.16deg, #FAFF00 -1.06%, #EC00E2 100%);

  &:hover {
    color: #000;
    background: transparent;
  }

  @include mobile{

    margin-right: 0;
    padding: 15px 10px;
  }
}

.features__content-btn--two {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  margin-top: 50px;
  text-transform: uppercase;
  color: #3E3E3E;
  width: 176px;
  height: 62px;
  border: 2px solid #F7F7F7;
  border-image: linear-gradient(to right, #ec00e011, #ec00e03d, #EC00E2);
  border-image-slice: 1;
  @include mobile{

    // padding: 15px 10px;
    width: 156px;
    height: 47px;
  }
}
.shoe__sb{
  position: relative;
  top: 150px;
  width: 600px;
  @include big-desktop{
    top: 280px;
    max-width: 350px;
  }

  @include tablet{

    display: none;
  }
}

