.nav {

  z-index: 1000000;

  @include tablet {

    position: absolute;
    width: 100%;
    text-align: center;
  }

  &__list {
    @include flex-v-center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 50px;
    }

    @include tablet {
      &:not(:last-child) {
        margin-right: 0px;
      }

      margin-top: 40px;
    }

    @include mobile {
      &:not(:last-child) {
        margin-right: 0px;
      }
    }
  }

  &__link {
    font-family: "Gilroy", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #3E3E3E;
  }

  &__list {
    margin-left: 150px;

    @include tablet {

      display: block;
      background: rgba(122, 145, 135, 0.5);
      backdrop-filter: blur(10px);
      border-radius: 10px;
      width: 96%;
      height: 60vh;
      z-index: 500000;
      position: absolute;
      transition: all .8s ease;
      margin: -650px auto 0;
    }

    @include mobile {

      margin-left: 0;
      margin: -650px auto 0;
      width: 88%;
      left: 0;
      right: 0;
    }
  }

  .menu--active {
    margin-top: 112px;

    @include for-desktop {

      margin-top: 0;

    }
  }

}
