.footer {
  height: 420px;
  color: #fff;
  background: #02083C;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  position: relative;
  top: 85px;

  @include mobile{
    justify-content: center;
  }
}
.footer__content-desc{
  @include mobile{
    margin: 0 auto;
  }
}

.footer__logo{
  margin: 0 auto;
  display: block;
}

.footer__content-text {
  font-family: "Gilroy", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-transform: capitalize;
  color: #FFFFFF;
}

.form__footer-label {
  position: relative;

  &::after {
    content: url(../../img/svg/arrow-input.svg);
    position: absolute;
    cursor: pointer;
    right: 0;
    top: 0;
  }
}

.button{
  position: absolute;
  bottom: -33px;
  width: 100%;
  left: 0;
}
.form__footer-input {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-transform: lowercase;
  color: #FFFFFF;
  opacity: 0.7;
  width: 295px;
  margin-top: 70px;
  margin-bottom: 37px;
  background: transparent;
  border-bottom: 1px solid rgb(255, 255, 255);

  &::placeholder {
    font-family: "Gilroy", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    text-transform: lowercase;
    color: #FFFFFF;
    opacity: 0.7;
  }
}
.footer__content-gethelp,
.footer__content-nav{
  @include tablet{
    display: none;
  }
}
.footer__content-social{
  @include mobile{
    position: absolute;
    margin: 0 auto;
    left: 0;
    right: 0;
    bottom: -53px;
  }
}

.footer__title-gethelp {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-transform: capitalize;
  color: #FFFFFF;
}
.footer__content-social{
  margin-right: 20px;
}
.footer__content-desc{
  margin-left: 20px;
}

.nav__footer-list {
  padding: 0;
}

.nav__footer-link {
  font-family: "Gilroy", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 36px;
  text-transform: capitalize;
  transition: all 0.2s ease;
  color: #FFFFFF;
  opacity: 0.8;

  &:hover {
    opacity: 1;
  }
}

.footer__title-nav {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-transform: capitalize;
  color: #FFFFFF;
}


.social-footer {
  display: flex;
  align-items: center;
}

.social__footer-link {
  margin-left: 30px;
}

.copyright {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-transform: capitalize;
  color: #FFFFFF;
  display: block;
  margin: 0 auto;
  text-align: center;
  margin-top: 130px;
  padding: 36px 0 0 0;
  border-top: 1px solid rgba(255, 255, 255, 0.5);

  @include mobile{
    font-size: 11px;
  }

}
