

.price__content-subtitle {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  text-transform: uppercase;
  background: linear-gradient(107.81deg, #0047FF -0.31%, #EC00E2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin-top: 167px;

  @include mobile{

    margin-top: 350px;
  }
}

.price__content-title {
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 72px;
  text-align: center;
  text-transform: uppercase;
  color: #1F170A;
}

.price__content-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include tablet{

    display: block;
    margin: 0 auto;
    text-align: center;
  }
}

.price__slide-box {
  max-width: 265px;
  max-height: 407px;

  @include tablet{

    max-width: 100%;
  }
}

.price__content-img {
  width: 237px;
  height: 187px;
}

.price__content-colurs {
  position: relative;
  left: 58px;

  @include mobile{

    left: 0;
  }
}



.price__slider-title {
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
}

.slider__price {
  font-family: "Gilroy", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FF3D00;
}

.price-btn {
  font-weight: 400;
  font-size: 16px;
  margin: 0 auto;
  text-align: center;
  display: block;
  text-transform: uppercase;
  color: #3E3E3E;
  width: 176px;
  height: 62px;
  background: linear-gradient(107.81deg, #0047FF -0.31%, #EC00E2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  border: 2px solid transparent;
  border-image: linear-gradient(to right, #0922b398, #ec00e03d, #EC00E2);
  border-image-slice: 1;
}

