.shop {
  background-image: url(../../img/shop-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: 300px;

  @include mobile{
    margin-top: 90px;
  }
}

.shop__content {
  margin: 0 auto;
  display: block;
  top: 199px;
  text-align: center;
  position: relative;
  background: rgba(255, 255, 255, 0.8);
  opacity: 0.75;
  backdrop-filter: blur(40px);
  padding: 40px;
  max-width: 620px;

  @include tablet {

    max-width: 330px;
  }
}

.shop__title {
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 72px;
  text-align: center;
  text-transform: uppercase;
  color: #1F170A;

  @include tablet{
    font-size: 55px;
  }
  @include mobile{
    font-size: 40px;
  }
}

.shop__btn {
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  width: 177px;
  height: 60px;
  background: linear-gradient(107.81deg, #0047FF -0.31%, #EC00E2 100%);
}
